import React, { useEffect, useState } from "react";
import "./styles.css";

export default function App() {
  const [heightT, setHeightT] = useState(500);

  const link =
    "http://localhost:9004/?platform=pariplay&moneyMode=fun&forceId=true&ticket=558888c2783c34-9ba9-4415-9fbc-4475537f9691&lang=en-US&gameCode=lobby&ukgcheader=0&gameLoadSuccess=false";
  const h = "200px";

  useEffect(() => {
    window.addEventListener(
      "message",
      function (e) {
        console.log("eee----", e.data);
        var iframe = document.getElementById("#myIframe");
        var eventName = e.data[0];
        var data = e.data[1];
        switch (eventName) {
          case "setHeight":
            setHeightT(data);
            break;
        }
      },
      false
    );
  }, []);

  return (
    <div className="App">
      <h1>Hello CodeSandbox</h1>
      <h2>Start editing to see some magic happen!</h2>

      <iframe
        id="#myIframe"
        scrolling="no"
        allowfullscreen=""
        width="90%"
        height={heightT + "px"}
        src={link}
      ></iframe>
    </div>
  );
}
